import { useWallet } from 'use-wallet';
import { BigNumber, Contract } from 'ethers';
import { useCallback, useEffect, useState } from 'react';

import useCore from '../useCore';
import { BasicState } from '../../utils/interface';
import useGetMyPurchases from './useGetMyPurchases';
import { LOADING_DEFAULT_BASIC_STATE, NON_LOADING_DEFAULT_BASIC_STATE } from '../../utils/constants';

const useGetClaimedAllocations = (token: string, id: string) => {
  const [state, setState] = useState<BasicState>(LOADING_DEFAULT_BASIC_STATE);

  const core = useCore();
  const { account } = useWallet();
  const myPurchases = useGetMyPurchases(token, id);

  const fetchValue = useCallback(async () => {
    if (myPurchases.isLoading) {
      setState(LOADING_DEFAULT_BASIC_STATE)
      return;
    };

    const contract: Contract = core.getTokenSaleSwapContract(token, id);

    const purchasesPromise = myPurchases.value.map(async (index: BigNumber) => {
      return await contract.purchases(index);
    });
    const purchases = await Promise.all(purchasesPromise);

    const purchasesAmounts = purchases
      .filter(purchase => purchase.wasFinalized)
      .filter(purchase => !purchase.reverted)
      .map(purchase => purchase.amount || BigNumber.from(0))
      .reduce((a, b) => a.add(b), BigNumber.from(0))

    setState({ isLoading: false, value: purchasesAmounts });
  }, [core, myPurchases, id, token]);

  useEffect(() => {
    if (core && account) {
      fetchValue().catch((err) => {
        setState(NON_LOADING_DEFAULT_BASIC_STATE);
        console.error(`Failed to fetch my claimable allocations of ${token}: ${err.stack}`)
      });
    } else {
      setState(NON_LOADING_DEFAULT_BASIC_STATE);
    }
  }, [myPurchases, core, account, id, fetchValue, token]);

  return state;
};

export default useGetClaimedAllocations;
