import styled from "styled-components";
import { useEffect, useState } from "react";
import ReCAPTCHA from "react-google-recaptcha";

import theme from "../../../theme";

import Modal from "../../../components/Modal";
import Button from "../../../components/Button";
import InfoTip from "../../../components/InfoTip";
import DataField from "../../../components/DataField";
import { CustomInput } from "../../../components/Input";
import AlertSnackbar from "../../../components/AlertSnackbar";
import { States, StatesMsg } from "../../../components/StatesBorder";

import callApi from "../../../utils/apiCaller";

interface IProps {
  openModal: boolean;
  onModalClose: () => void;
}

const EmailModal = (props: IProps) => {
  const [name, setName] = useState<string>("");
  const [email, setEmail] = useState<string>("");
  const [sending, setSending] = useState<boolean>(false);
  const [wrongEmail, setWrongEmail] = useState<boolean>(false);
  const [blankField, setBlankField] = useState<boolean>(false);
  const [reCaptcha, setReCaptcha] = useState<boolean>(false);
  const [reCaptchaError, setreCaptchaError] = useState<string>("");
  const [reCaptchaToken, setreCaptchaToken] = useState<string>("");
  const [response, setResponse] = useState<"subscribed" | "error" | "success" | "">("");

  useEffect(() => {
    if (!props.openModal) {
      setName("");
      setWrongEmail(false);
      setBlankField(false);
      setEmail("");
      setResponse("");
      setSending(false);
      setReCaptcha(false);
      setreCaptchaError("");
      setreCaptchaToken("");
    }
  }, [props.openModal])

  const validate = () => {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (email === "" || name === "") {
      setBlankField(true);
      return false;
    } else if (!re.test(String(email).toLowerCase())) {
      setWrongEmail(true);
      return false;
    }

    return true;
  }

  const fetchAPY = () => {
    callApi("subscribe", "POST", { email: email, name: name, recaptcha_token: reCaptchaToken })
      .then(res => {
        props.onModalClose();
        setEmail("");
        setWrongEmail(false);
        setSending(false);
        if (res?.success) {
          setResponse("success");
        } else {
          if (res?.error === 'Email already exists') setResponse("subscribed");
          else setResponse("error");
        }
      })
      .catch((err) => {
        props.onModalClose();
        setEmail("");
        setWrongEmail(false);
        setResponse("error")
        setSending(false);
      });
  }

  const onSubmit = () => {
    setWrongEmail(false);
    setBlankField(false);
    if (validate()) {
      setSending(true);
      fetchAPY();
    }
  }

  const onReCaptchaChange = (value: any) => {
    console.log("Captcha value:", value);
    setreCaptchaToken(value);
    setReCaptcha(true);
  }

  return (
    <div>
      <AlertSnackbar
        open={response === "success"}
        type={"success"}
        subTitle={"You subscribed successfully. You will get updates on your email."}
        handleCancel={() => setResponse("")}
      />
      <AlertSnackbar
        open={response === "subscribed"}
        type={"alert"}
        subTitle={"You have already subscribed with this email id."}
        handleCancel={() => setResponse("")}
      />
      <AlertSnackbar
        open={response === "error"}
        type={"alert"}
        subTitle={"Internal Server Error :( Please try again later"}
        handleCancel={() => setResponse("")}
      />
      <Modal
        open={props.openModal}
        handleClose={() => props.onModalClose()}
        title={"Subscribe for updates"}
        closeButton
      >
        <div className="m-b-16">
          <DataField
            label={"Full Name"}
            labelFontColor={theme.color.transparent[100]}
            className={"m-b-8"}
          />
          <States state={"default"}>
            <InputContainer>
              <CustomInput
                inputMode={"text"}
                placeholder={"Full Name"}
                value={name}
                disabled={false}
                type={'string'}
                onChange={(event) => {
                  setBlankField(false);
                  setName(event.target.value);
                }}
              />
            </InputContainer>
          </States>
        </div>
        <div className="m-b-24">
          <DataField
            label={"Enter Email Address"}
            labelFontColor={theme.color.transparent[100]}
            className={"m-b-8"}
          />
          <States state={wrongEmail ? "error" : "default"}>
            <InputContainer>
              <CustomInput
                inputMode={"text"}
                placeholder={"Enter Your Email"}
                value={email}
                disabled={false}
                type={'string'}
                onChange={(event) => {
                  setWrongEmail(false);
                  setBlankField(false);
                  setEmail(event.target.value);
                }}
              />
            </InputContainer>
          </States>
          {wrongEmail && <StatesMsg state={"error"}>
            Enter a valid email ID
          </StatesMsg>}
        </div>
        <div className="m-b-12">
          <ReCAPTCHA
            sitekey="6Le5EN4cAAAAAOUkLfTyO8YFel-JJJi9HLOQ8jUh"
            onChange={onReCaptchaChange}
            onErrored={() => {
              setreCaptchaError("Some Internal Captcha Error Occured. Please try again.")
              setReCaptcha(false)
            }}
            onExpired={() => {
              setreCaptchaError("This is Captcha has expired. Please try again.")
              setReCaptcha(false)
            }}
          />
        </div>
        {reCaptchaError && <InfoTip type={"Warning"} msg={reCaptchaError} className="m-b-12" />}
        {blankField && <InfoTip type={"Warning"} msg={"Field cannot be blank"} className="m-b-12" />}
        <Button
          loading={sending}
          onClick={() => onSubmit()}
          tracking_id={"subscribe"}
          disabled={!reCaptcha}
        >
          Subscribe
        </Button>
      </Modal>
    </div>
  );
}

export default EmailModal;

const InputContainer = styled.div`
  background: ${theme.color.dark[500]};
  border-radius: 6px;
`;
