import { BigNumber, Contract } from "ethers";
import { useCallback, useEffect, useState } from "react";

import useCore from "../useCore";
import { useBlockNumber } from "../../state/application/hooks";
import { useWallet } from "use-wallet";

const useRefundCanRefund = (token: string, id: string) => {
  const [state, setState] = useState<{ isLoading: boolean; canRefund: boolean }>({
    isLoading: false,
    canRefund: false,
  });

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();

  const fetchValue = useCallback(async () => {
    const contract: Contract = core.getTokenSaleSwapRefundContract(token, id);
    const val = await contract.canRefund(account);

    setState({ isLoading: false, canRefund: val });
  }, [core, id, token, account]);

  useEffect(() => {
    if (core) {
      fetchValue().catch((err) => {
        // setState(NON_LOADING_DEFAULT_DATE_STATE);
        console.error(`Failed to fetch start and end date of sale for ${token}: ${err.stack}`);
      });
    } else {
      // setState( );
    }
  }, [core, id, fetchValue, token, blockNumber]);

  return state;
};

export default useRefundCanRefund;
