import styled from "styled-components";
import { useLocation } from "react-router-dom";
import { useMediaQuery } from "react-responsive";
import React, { useEffect, useState } from "react";
import { ChainUnsupportedError, useWallet } from "use-wallet";

import IconLoader from "../IconLoader";
import { Mixpanel } from "../../analytics/Mixpanel";

import Button from "../Button";
import AlertSnackbar from "../AlertSnackbar";
import MobileNav from "./components/MobileNav";
import AccountButton from "./components/AccountButton";

import useCore from "../../hooks/useCore";
import useGaTracker from "../../analytics/useGATracker";
import DesktopTransactionInfo from "./components/modal/Transaction/DesktopTransactionInfo";
import DesktopProjectRoutes from './components/modal/ProjectRoutes/DesktopProjectRoute';
import MobileProjectRoutes from './components/modal/ProjectRoutes/MobileProjectRoute';
import ChooseWallet from "./components/modal/WalletInfo/ChooseWallet";

import DesktopLogo from "../../assets/images/MAHAStarterlg.png";
import TextWrapper from "../TextWrapper";
import theme from "../../theme";
import '../../scripts/headerWarning.js'
import BuyMAHA from "../../views/Home/modals/BuyMAHA";

const TopBar: React.FC = () => {
  const [showMobileMenu, toggleMobileMenu] = useState(false);
  const [showTxModal, setShowTxModal] = useState<boolean>(false);
  const [showWarning, setShowWarning] = React.useState<boolean>(false);
  const [buyMAHAModal, setButMAHAModal] = useState<boolean>(false);
  const [showProjectModal, setShowProjectModal] = useState<boolean>(false);
  const [showWalletOption, setShowWalletOption] = useState<boolean>(false);

  useGaTracker();
  const core = useCore();
  const location = useLocation();
  const isMobile = useMediaQuery({ maxWidth: "600px" });
  const { account, ethereum, error }: { account: any, ethereum?: any, error: any } = useWallet();

  useEffect(() => {
    Mixpanel.track(`ScreenView:${location.pathname}`);
  }, [location]);

  useEffect(() => {
    if (account) {
      Mixpanel.identify(account);
      Mixpanel.people.set({ walletId: account });
    }
  }, [account]);

  useEffect(() => {
    // @ts-ignore
    const { ethereum: web3Ethereum }: { ethereum?: any } = window;

    if (web3Ethereum)
      web3Ethereum?.on("chainChanged", () => {
        window.location.reload();
      });
  }, [ethereum]);

  useEffect(() => {
    if (error instanceof ChainUnsupportedError) {
      setShowWarning(true);
    } else {
      setShowWarning(false);
    }
  }, [error]);

  return (
    <TopBarContainer>
      <ChooseWallet
        openModal={showWalletOption}
        onClose={() => {
          setShowWalletOption(false)
        }}
      />
       <DesktopTransactionInfo
            openModal={showTxModal}
            onDismiss={() => setShowTxModal(false)}
          />
      {isMobile
        ? <MobileProjectRoutes openModal={showProjectModal} onDismiss={() => setShowProjectModal(false)} />
        : <DesktopProjectRoutes openModal={showProjectModal} onDismiss={() => setShowProjectModal(false)} />
      }
      <BuyMAHA
        openModal={buyMAHAModal}
        onModalClose={() => setButMAHAModal(false)}
      />
      <AlertSnackbar
        open={showWarning}
        title={"Wrong Network!"}
        subTitle={
          `You are on the wrong network, switch/add ${core.config.networkName} Network to use the app.`
        }
      />
      <StyledTopBar>
        {!account && <WarningMsg id={"WarningMsg"}>
          <div className={"single-line-center-center mo-single-line-column"} >
            <TextWrapper
              text={"Please make sure you are on BSC Mainnet Network."}
              align={"center"}
            />
            <div
              onClick={() => window.open("https://docs.binance.org/smart-chain/developer/rpc.html#mainnetchainid-0x38-56-in-decimal")}
            >
              <TextWrapper
                text={"Check RPC details here."}
                Fcolor={theme.color.primary[300]}
                className={"m-l-4 pointer"}
                align={"center"}
              />
            </div>
          </div>
        </WarningMsg>}
        <StyledTopBarInner>

          <HideonPhone>
            <div className="single-line-center-between">
              <div className="dialog-class pointer" onClick={() => window.location.href = "/#/"}>
                <img
                  src={DesktopLogo}
                  width={269}
                  height={44}
                  alt={"logo"}
                />
              </div>
              <div className="single-line-center-start">
              <IconLoader className={"pointer m-r-24"} iconName={'BentoMenu'} iconType={'misc'} onClick={() => {setShowProjectModal(true)}}/>
                {
                  !!account
                    ?
                    <IconLoader
                      iconName={"Transaction"}
                      className={"pointer m-r-12"}
                      onClick={() => setShowTxModal(true)}
                    />
                    :
                    <div />
                }
                <div className={"m-r-12"}>
                  <Button
                    tracking_id={"buy_MAHA_modal"}
                    onClick={() => setButMAHAModal(true)}
                  >
                    Buy MAHA
                  </Button>
                </div>
                <AccountButton showWarning={showWarning} />
              </div>
            </div>
          </HideonPhone>
          <HideOnBigScreen>
          {!account ? (
            <div className="single-line-center-between">
              <IconLoader iconName={'Starterlg'} iconType={'brandLogo'} onClick={() => window.location.href = "/#/"}/>
              <div className="p-l-4">
              <Button
                  text="Connect"
                  tracking_id={"connect_wallet"}
                  variant={'transparent'}
                  onClick={() => {
                    setShowWalletOption(true)
                  }}
                >
                <IconLoader iconName={'Wallet'} width={24} height={24} className="m-r-8" />
                </Button>
              </div>
            </div>
          ) : (
            <div className="single-line-center-between">
              <IconLoader
                iconName={!showMobileMenu ? 'Menu' : 'Cross'}
                onClick={() => toggleMobileMenu(!showMobileMenu)}
                className={'pointer'}
              />
              <IconLoader iconName={'Starterlg'} iconType={'brandLogo'} onClick={() => window.location.href = "/#/"}/>
              <IconLoader iconName={'BentoMenu'} iconType={'misc'} onClick={() => {setShowProjectModal(true)}}/>
            </div>
            )}
          </HideOnBigScreen>
          <HideOnBigScreen>
            <MobileNav
              openMenu={showMobileMenu}
              showWarning={showWarning}
              isMainnet={true}
              onClick={() => toggleMobileMenu(!showMobileMenu)}
              onWalletClick={() => { }}
            />
          </HideOnBigScreen>
        </StyledTopBarInner>
      </StyledTopBar>
    </TopBarContainer>
  );
};

const TopBarContainer = styled.div`

  position: fixed;
  z-index: 100;
  display: flex;
  flex-direction: column;
  width: 100%;
  top: 0;
`;

const HideonPhone = styled.div`
  width: 100%;
  display: block;
  @media (max-width: 600px) {
    display: none;
  } ;
`;

const HideOnBigScreen = styled.div`
  width: 100%;
  display: none;
  @media (max-width: 600px) {
    display: block;
  } ;
`;

const StyledTopBar = styled.div`
  box-sizing: border-box;
  margin: 0 auto;
  width: 100%;
  background: rgba(0, 0, 0);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
`;

const StyledTopBarInner = styled.div`
  align-content: center;
  display: flex;
  height: 72px;
  justify-content: space-between;
  width: 100%;
  padding: 0 60px;
  flex-wrap: wrap;
  @media (max-width: 600px) {
    padding: 0 16px;
  }
`;

const WarningMsg = styled.div`
  display: block;
  background-color: #2A2827;
  padding: 12px 16px;
`


export default TopBar;
