import { ethers } from 'ethers';
import { useWallet } from 'use-wallet';
import { Grid } from "@material-ui/core";
import { useState, useMemo, useEffect } from "react";
import { BigNumber } from "@ethersproject/bignumber";

import Modal from "../../../components/Modal";
import Input from "../../../components/Input";
import Button from "../../../components/Button";
import InfoTip from "../../../components/InfoTip";
import IconLoader from "../../../components/IconLoader";
import InputContainer from "../../../components/InputContainer";
import { States, StatesMsg } from "../../../components/StatesBorder";
import CollateralDropDown from "../../../components/CollateralDropDown";

import useCore from "../../../hooks/useCore";
import useBalance from "../../../hooks/useBalance";
import useInvest from "../../../hooks/callbacks/useInvest";
import useGetMyAllocations from "../../../hooks/state/useGetMyAllocations";
import useGetTokenSalePrice from "../../../hooks/state/useGetTokenSalePrice";
import useGetMinAndMaxAllocation from "../../../hooks/state/useGetMinAndMaxAllocation";
import {
  formatToBN,
  formatToBNToken,
  getDisplayBalance,
  getDisplayBalanceToken
} from "../../../utils/formatBalance";
import { MAX_UINT_256 } from "../../../utils/constants";

interface Iprops {
  tokenSymbol: string
  modalOpen: boolean;
  id: string;
  buttonLoading: boolean;
  onMining: () => void;
  onModalClose: () => void;
  onSuccess: () => void;
  onMiningClose: () => void;
  onSuccessClose: () => void;
  setButtonLoading: (state: boolean) => void;
}

const InvestModal = (props: Iprops) => {
  const {
    modalOpen,
    onModalClose,
    onSuccess,
    tokenSymbol,
    id,
    onMining,
    setButtonLoading,
    buttonLoading,
    onMiningClose,
    onSuccessClose
  } = props;

  const [investValue, setInvestValue] = useState<string>("");
  const [receiveValue, setReceiveValue] = useState<string>("");

  const core = useCore();
  const balance = useBalance();
  const { account } = useWallet();
  const tradeValue = useGetTokenSalePrice(tokenSymbol, id);
  const myAllocation = useGetMyAllocations(tokenSymbol, id);
  const minAndMaxAllocation = useGetMinAndMaxAllocation(tokenSymbol, id);

  useEffect(() => {
    setInvestValue('');
    setReceiveValue('');
  }, [account]);

  const frontEndRate = ethers.utils.parseEther(`1`).mul(3303964758).div(1e10).div(1e3);

  const investHandler = useInvest(
    tokenSymbol,
    formatToBN(investValue, 18).add(1).mul(BigNumber.from(10).pow(core.tokens[tokenSymbol].decimal)).div(tradeValue.value.gt(0) ? tradeValue.value : BigNumber.from(1)),
    formatToBN(investValue, 18),
    id
  );

  const txIsMininingModalCb = () => {
    onMining();
    setButtonLoading(true);
    const timeout = setTimeout(() => onMiningClose(), core.config.modalCloseIntervals);
    return () => clearTimeout(timeout);
  }

  const txIsSuccessfullCb = () => {
    onSuccess();
    setButtonLoading(false);
    const timeout = setTimeout(() => onSuccessClose(), core.config.modalCloseIntervals);
    return () => clearTimeout(timeout);
  }

  const txHasFailedCb = () => {
    setButtonLoading(false);
  }

  const onInvestClick = () => investHandler(
    txIsMininingModalCb,
    txIsSuccessfullCb,
    txHasFailedCb
  );

  const yourAllocationsFormatted = useMemo(() => {
    return Number(getDisplayBalanceToken(myAllocation.value.mul(100).div(40), core.tokens[tokenSymbol], 3))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })
  }, [myAllocation, core, tokenSymbol]);

  const balanceFormatted = useMemo(() => {
    return Number(getDisplayBalance(balance.value, 18, 3))
      .toLocaleString('en-US', { maximumFractionDigits: 2, minimumFractionDigits: 2 })
  }, [balance]);

  const isInvestmentLessThanMin = useMemo(() => {
    const inputVal = formatToBNToken(receiveValue, core.tokens[tokenSymbol]);
    const maxAllocationVal = minAndMaxAllocation.minAllocation.mul(100).div(40);
    return inputVal.lt(maxAllocationVal);
  }, [minAndMaxAllocation, core, receiveValue, tokenSymbol]);

  const isInvestmentMoreThanMax = useMemo(() => {
    const inputVal = formatToBNToken(receiveValue, core.tokens[tokenSymbol]);
    const previousVal = myAllocation.value.mul(100).div(40);
    const maxAllocationVal = minAndMaxAllocation.maxAllocation.mul(100).div(40);
    return inputVal.add(previousVal).gt(maxAllocationVal);
  }, [minAndMaxAllocation, core, receiveValue, tokenSymbol, myAllocation]);

  const isAmountMoreThanBalance = useMemo(() => {
    return formatToBN(investValue, 18).gt(balance.value);
  }, [balance, investValue]);

  const disableButton = useMemo(() => {
    return !Number(investValue) ||
      !Number(receiveValue) ||
      isInvestmentLessThanMin ||
      isInvestmentMoreThanMax;
  }, [investValue, isInvestmentMoreThanMax, isInvestmentLessThanMin, receiveValue]);

  const onInputChange = (data: string) => {
    setInvestValue(data);

    const bnInput: BigNumber = formatToBN(data, 18);
    if (bnInput.eq(0) || tradeValue.value.lte(0)) {
      setReceiveValue('');
      return;
    }

    const value: BigNumber = bnInput
      .mul(BigNumber.from(10).pow(core.tokens[tokenSymbol].decimal))
      .div(frontEndRate);

    setReceiveValue(getDisplayBalanceToken(value, core.tokens[tokenSymbol], 18));
  }

  const onRecieveChange = (data: string) => {
    setReceiveValue(data);
    const bnRecieve: BigNumber = formatToBNToken(data, core.tokens[tokenSymbol]);
    if (bnRecieve.eq(0)) {
      setInvestValue('');
      return;
    }

    const value: BigNumber = frontEndRate
      .mul(bnRecieve)
      .div(BigNumber.from(10).pow(core.tokens[tokenSymbol].decimal));

    setInvestValue(getDisplayBalance(value, 18, 18));
  }

  const onInputMaxChange = () => {
    const inputValue: string = getDisplayBalance(balance.value, 18, 18);

    let bnInput: BigNumber = formatToBN(inputValue, 18);
    if (bnInput.eq(0) || tradeValue.value.lte(0)) {
      setReceiveValue('');
      return;
    }

    const maxInputAmountAllowed: BigNumber = minAndMaxAllocation.maxAllocation.gt(myAllocation.value)
      ? minAndMaxAllocation.maxAllocation.mul(100).div(40).sub(myAllocation.value.mul(100).div(40))
      : MAX_UINT_256;  // Fail safe condition, should never trigger.

    const maxInputValue = frontEndRate
      .mul(maxInputAmountAllowed)
      .div(BigNumber.from(10).pow(core.tokens[tokenSymbol].decimal));

    if (bnInput.gt(maxInputValue)) bnInput = maxInputValue;

    const value: BigNumber = bnInput.sub(1)
      .mul(BigNumber.from(10).pow(core.tokens[tokenSymbol].decimal))
      .div(frontEndRate);

    setInvestValue(getDisplayBalance(bnInput.sub(1), 18, 18));
    setReceiveValue(getDisplayBalanceToken(value, core.tokens[tokenSymbol], 18));
  }

  return (
    <Modal
      closeButton
      title={`Invest in ${id.toUpperCase()}`}
      open={modalOpen}
      handleClose={() => onModalClose()}
    >
      {
        isInvestmentLessThanMin && (
          <InfoTip
            type={'Error'}
            msg={'Investment cannot be less than minimum individual amount.'}
          />
        )
      }
      {
        isInvestmentMoreThanMax && (
          <InfoTip
            type={'Error'}
            msg={'Investment cannot be more than maximum individual amount.'}
          />
        )
      }
      <div className={'m-t-20'}>
        <InputContainer
          label={"Your Investment"}
          dataLabel={"Balance: "}
          dataValue={balanceFormatted}
        >
          <div>
            <States className="single-line-center-start" state={isAmountMoreThanBalance ? "error" : "default"}>
              <Input
                value={investValue}
                setValue={onInputChange}
                maxTag={true}
                onMaxClick={onInputMaxChange}
              />
              <CollateralDropDown
                selectedSymbol={"BNB"}
              />
            </States>
            {
              isAmountMoreThanBalance &&
              <StatesMsg state={"error"}>
                Amount cannot be more than your balance.
              </StatesMsg>
            }
          </div>
        </InputContainer>
        <div className="single-line-center-center m-t-8 m-b-8 ">
          <IconLoader iconName={"ArrowTailDown"} iconType={"arrow"} />
        </div>
        <InputContainer
          label={"Your Investment"}
          dataLabel={"Invested: "}
          dataValue={yourAllocationsFormatted}
        >
          <div className="single-line-center-start">
            <Input
              value={receiveValue}
              setValue={onRecieveChange}
            />
            <CollateralDropDown
              selectedSymbol={tokenSymbol}
            />
          </div>
        </InputContainer>
        <div className="m-t-32">
          <Grid container spacing={2}>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                variant={"transparent"}
                onClick={onModalClose}
              >
                Cancel
              </Button>
            </Grid>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                loading={buttonLoading}
                disabled={disableButton}
                onClick={onInvestClick}
                tracking_id="confirm_invest_now"
              >
                Invest Now
              </Button>
            </Grid>
          </Grid>
        </div>
      </div>
    </Modal>
  );
}

export default InvestModal;
