import { useMemo } from "react";
import Loader from "react-spinners/BeatLoader";

import theme from "../../theme";

import DataField from "../DataField";

import useCore from "../../hooks/useCore";
import { getDisplayBalanceToken } from "../../utils/formatBalance";
import { IProjectIdProp, ITokenSymbolProp } from "../../utils/interface";
import useGetClaimableAllocations from "../../hooks/state/useGetClaimableAllocations";

const Claimable = (props: ITokenSymbolProp & IProjectIdProp) => {
  const { tokenSymbol, id } = props;

  const core = useCore();
  const redeemAbleIdAndAmount = useGetClaimableAllocations(tokenSymbol, id);

  const yourRedeemableAmountFormatted = useMemo(() => {
    return Number(getDisplayBalanceToken(redeemAbleIdAndAmount.value, core.tokens[tokenSymbol], 3))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })
  }, [redeemAbleIdAndAmount, core, tokenSymbol]);

  return (
    <div>
      <DataField
        label={"Current Claimable"}
        value={`${yourRedeemableAmountFormatted} ${tokenSymbol}`}
        isValueLoading={redeemAbleIdAndAmount.isLoading}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
        className={"m-b-16"}
      />
    </div>
  );
}

export default Claimable;
