import { useWallet } from "use-wallet";
import { useCallback, useEffect, useState } from 'react';

import { BasicStateString } from "../../utils/interface";
import callApi from "../../utils/apiCaller";
import { LOADING_DEFAULT_BASIC_STATE_STRING, NON_LOADING_DEFAULT_BASIC_STATE_STRING } from "../../utils/constants";
import useGetUsdBinanceValue from "./useGetUsdBinanceValue";

const useConvertUSDtoBNB = (value: string) => {
  const [state, setState] = useState<BasicStateString>(LOADING_DEFAULT_BASIC_STATE_STRING);

  const { account } = useWallet();
  const getUSDValue = useGetUsdBinanceValue();

  const fetchValue = useCallback(async () => {
    if (getUSDValue.isLoading) setState(LOADING_DEFAULT_BASIC_STATE_STRING);
    else {
      setState({
        isLoading: false,
        value: String(Number(value)/Number(getUSDValue.value))
      })
    }
  }, [getUSDValue]);

  useEffect(() => {
    fetchValue()
      .catch((err) => {
        setState(NON_LOADING_DEFAULT_BASIC_STATE_STRING);
        console.error(`Failed to fetch balance of MAHAX: ${err.stack}`)
      });
  }, [account, fetchValue]);

  return state;
};

export default useConvertUSDtoBNB;
