import { useWallet } from "use-wallet";
import { useEffect, useMemo, useState } from "react";

import Ended from "./Ended";
import Started from "./Started";
import IDOCountdown from "./IDOCountdown";

import callApi from "../../../utils/apiCaller";
import { IProjectIdProp, ITokenSymbolProp } from "../../../utils/interface";
import useGetStartAndEndDate from "../../../hooks/state/useGetStartAndEndDate";

export interface IuserStatus {
  isLoading: boolean;
  value: "Not Registered" | "Registered";
}

export const initUserStatus: IuserStatus = {
  isLoading: true,
  value: "Registered",
}

export interface IkycStatus {
  isLoading: boolean;
  value: 'incomplete' | 'waiting' | 'approved' | 'inreview' | 'rejected';
}

export const initKycStatus: IkycStatus = {
  isLoading: true,
  value: "incomplete",
}

const RightSection = (props: ITokenSymbolProp & IProjectIdProp) => {
  const { tokenSymbol, id } = props;

  const startAndEndDate = useGetStartAndEndDate(tokenSymbol, id);

  const status: string = useMemo(() => {
    if (startAndEndDate.startDate * 1000 > Date.now()) return "upcoming";
    if (Date.now() < startAndEndDate.endDate * 1000) return "live";
    return "closed";
  }, [startAndEndDate]);

  if (startAndEndDate.isLoading) return null

  return (
    <div>
      <IDOCountdown
        status={status}
        tokenSymbol={tokenSymbol}
        id={id.toUpperCase()}
        startAndEndDate={startAndEndDate}
      />
      <Started status={status} id={id.toLowerCase()} tokenSymbol={tokenSymbol} />
      <Ended status={status} id={id.toLowerCase()} tokenSymbol={tokenSymbol} />
    </div>
  );
}

export default RightSection;
