import {useMemo} from "react";
import {BigNumber} from "ethers";
import Loader from "react-spinners/BeatLoader";

import theme from "../../theme";

import ProgressBar from "../ProgressBar";

import {IProjectIdProp, ITokenSymbolProp} from "../../utils/interface";
import {getDisplayBalance} from "../../utils/formatBalance";
import useGetTokensForSale from "../../hooks/state/useGetTokensForSale";
import useGetTokensAllocated from "../../hooks/state/useGetTokensAllocated";
interface Iprop{
  ids?: []
  isHome?: boolean
}

const PercentCompleted = (props: ITokenSymbolProp & IProjectIdProp & Iprop ) => {
  const tokensForSale = useGetTokensForSale(props.tokenSymbol, props.id);
  const tokensAllocated = useGetTokensAllocated(props.tokenSymbol, props.id);
 
  const percentCompleted = useMemo(() => {
    if (tokensAllocated.value.gt(tokensForSale.value)) return BigNumber.from(1e6).mul(100);

    if (tokensForSale.value.lte(0) || tokensAllocated.value.lte(0)) return BigNumber.from(100).mul(1e6).mul(0);
    return tokensAllocated.value
      .mul(1e6)
      .mul(100)
      .div(tokensForSale.value)
  }, [tokensAllocated, tokensForSale]);

  return (
    <div>
      {
        tokensAllocated.isLoading || tokensForSale.isLoading
          ? <Loader color={'#ffffff'} loading={true} size={4} margin={2}/>
          : (
            <div className="m-b-4">
              {props.id === "scallop" ?
                (
                  <ProgressBar
                    barColor={theme.color.green[300]}
                    percent={
                      Number(getDisplayBalance(percentCompleted, 6, 4))
                    }
                    width={'100%'}
                  />)
                : (
                  <ProgressBar
                    barColor={theme.color.primary[400]}
                    percent={
                      Number(getDisplayBalance(percentCompleted, 6, 4))
                    }
                    width={'100%'}
                  />
                )
              }

            </div>
          )
      }
    </div>
  );
}

export default PercentCompleted;
