import { useWallet } from "use-wallet";
import { useMemo, useState } from "react";
import useRefund from "../../../hooks/callbacks/useRefund";

import theme from "../../../theme";

import Button from "../../../components/Button";
import InfoTip from "../../../components/InfoTip";
import Divider from "../../../components/Divider";
import Claimable from "../../../components/Claimable";
import DataField from "../../../components/DataField";
import TokenPrice from "../../../components/TokenPrice";
import TotalRaised from "../../../components/TotalRaised";
import AmountRaised from "../../../components/AmountRaised";
import TotalAllocation from "../../../components/TotalAllocation";
import PercentCompleted from "../../../components/PercentCompleted";

import useCore from "../../../hooks/useCore";
import SuccessModal from "../../../components/SuccesModal";
import useRedeem from "../../../hooks/callbacks/useRedeem";
import { getDisplayBalanceToken } from "../../../utils/formatBalance";
import useGetMyAllocations from "../../../hooks/state/useGetMyAllocations";
import useGetClaimedAllocations from "../../../hooks/state/useGetClaimedAllocations";
import { IProjectIdProp, IStatusProps, ITokenSymbolProp } from "../../../utils/interface";
import useGetClaimableAllocationts from "../../../hooks/state/useGetClaimableAllocations";
import TokenAllocationNumbersInToken from "../../../components/TokenAllocationNumbersInToken";
import useRefundCanRefund from "../../../hooks/state/useRefundCanRefund";

const Ended = ({ tokenSymbol, status, id }: ITokenSymbolProp & IStatusProps & IProjectIdProp) => {
  const [miningModal, setMiningModal] = useState<boolean>(false);
  const [successModal, setSuccessModal] = useState<boolean>(false);
  const [buttonLoading, setButtonLoading] = useState<boolean>(false);

  const core = useCore();
  const { account } = useWallet();
  const myAllocation = useGetMyAllocations(tokenSymbol, id);
  const claimedAllocation = useGetClaimedAllocations(tokenSymbol, id);
  const claimableAllocations = useGetClaimableAllocationts(tokenSymbol, id);
  const canrefund = useRefundCanRefund(tokenSymbol, id)

  const claimHandler = useRedeem(
    tokenSymbol,
    myAllocation.value,
    id
  );

  const yourClaimedAmountFormatted = useMemo(() => {
    return Number(getDisplayBalanceToken(claimedAllocation.value, core.tokens[tokenSymbol], 3))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })
  }, [claimedAllocation, core, tokenSymbol]);

  const txIsMininingModalCb = () => {
    setMiningModal(true);
    setButtonLoading(true);
    const timeout = setTimeout(() => setMiningModal(false), core.config.modalCloseIntervals);
    return () => clearTimeout(timeout);
  }

  const txIsSuccessfullCb = () => {
    setMiningModal(false);
    setButtonLoading(false);
    setSuccessModal(true);
    // const timeout = setTimeout(() => setSuccessModal(false), core.config.modalCloseIntervals);
    // return () => clearTimeout(timeout);
  }

  const txHasFailedCb = () => {
    setSuccessModal(false);
    setButtonLoading(false);
  }

  const onClaimClick = () => {
    claimHandler(txIsMininingModalCb, txIsSuccessfullCb, txHasFailedCb);
  };

  const refundHandler = useRefund(
    tokenSymbol,
    myAllocation.value,
    id
  );

  const onRefundClick = () => {
    refundHandler(txIsMininingModalCb, txIsSuccessfullCb, txHasFailedCb);
  };



  if (status !== "closed" || !!!account) return <></>;


  return (
    <div className={"material-primary m-b-24"}>
      <SuccessModal
        modalOpen={miningModal}
        setModalOpen={() => setMiningModal(false)}
        title={"Tx submitted! Your transaction is being mined on the blockchain"}
        subsubTitle={"On transaction completion you can stake Forward tokens to earn rewards"}
      />
      {/* <SuccessModal
        modalOpen={successModal}
        setModalOpen={() => setSuccessModal(false)}
        title={"Congratulations! You have successfully contributed in the Forward sale."}
        subsubTitle={"You can now stake Forward tokens to earn rewards"}
        buttonText={"Stake Forward"}
        buttonType={"default"}
        buttonHref={"https://app.forwardprotocol.io/"}
      /> */}
      <div>
        {/* <div className={"m-b-16"}>
          <AmountRaised tokenSymbol={tokenSymbol} id={id} />
          <PercentCompleted tokenSymbol={tokenSymbol} id={id} />
          <TokenAllocationNumbersInToken tokenSymbol={tokenSymbol} id={id} />
        </div> */}
        {/* <TotalRaised tokenSymbol={tokenSymbol} id={id} /> */}
        <TokenPrice isDataField={true} tokenSymbol={tokenSymbol} />
        <Divider />
        <TotalAllocation tokenSymbol={tokenSymbol} id={id} />
        <Claimable tokenSymbol={tokenSymbol} id={id} />
        <DataField
          label={"Claimed"}
          value={`${yourClaimedAmountFormatted} ${tokenSymbol}`}
          labelFontSize={18}
          labelFontColor={theme.color.transparent[100]}
          valueFontSize={18}
          valueFontColor={"white"}
          valueFontWeight={600}
        />
        <div className="m-t-32">

          <InfoTip type={"Info"} msg={"If you would like to keep your allocation, you can claim your tokens when the protocol goes live."} className="m-b-8" />
          {
            <Button
              loading={buttonLoading}
              onClick={onRefundClick}
              disabled={false}>
              Claim Forward
            </Button>
          }

          {/* {
            !myAllocation.isLoading && myAllocation.value.lte(0) && account &&
            <InfoTip type={"Warning"} msg={"You have no allocation to claim."} className="m-b-8" />
          } */}
          {/* {
            !myAllocation.isLoading && myAllocation.value.gt(0) && claimableAllocations.value.lte(0) && account &&
            <InfoTip type={"Warning"} msg={"You have already claimed your current claimable allocation."} className="m-b-8" />
          } */}
          {/*{myAllocation.isLoading || claimableAllocations.isLoading
            ? null
            : (
              <div>
                <Button
                  variant={"transparent"}
                  loading={buttonLoading}
                  onClick={onClaimClick}
                  disabled={myAllocation.value.lte(0) || claimableAllocations.value.lte(0)}>
                  Claim {tokenSymbol}
                </Button>
                <div className="m-t-12">
                  <Button
                    onClick={() => window.open("https://app.forwardprotocol.io/")}
                  >
                    Stake {tokenSymbol}
                  </Button>
                </div>

              </div>
            )
          }*/}
        </div>

      </div>
    </div>
  );
}

export default Ended;
