import styled from "styled-components";

import theme from '../../theme';

interface StatesProps {
  state: 'error' | 'warning' | 'default';
};

export const States = styled.div<StatesProps>`
  border: ${({ state }) => {
    let color = "none";
    if (state === 'error') {
      color = `1px solid ${theme.color.red[300]}`;
    } else if (state === 'warning') {
      color = `1px solid ${theme.color.yellow[300]}`;
    }
    return color
  }};
  border-radius: 6px;
`;

export const StatesMsg = styled.p<StatesProps>`
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 130%;
  margin-top: ${({ state }) => state === 'default' ? 0 : 4}px;
  margin-bottom: 0;
  color: ${({ state }) => {
    let color = "transparent";
    if (state === 'error') {
      color = `${theme.color.red[300]}`;
    } else if (state === 'warning') {
      color = `${theme.color.yellow[300]}`;
    }
    return color
  }};;
`;
