import { BigNumber, Contract } from 'ethers';
import { useCallback, useEffect, useState } from 'react';

import useCore from '../useCore';
import { BasicState } from '../../utils/interface';
import { useBlockNumber } from '../../state/application/hooks';
import { LOADING_DEFAULT_BASIC_STATE, NON_LOADING_DEFAULT_BASIC_STATE } from '../../utils/constants';

const useGetTokensAllocated = (token: string, id: string, isHome?: boolean, ids?: []) => {
  const [state, setState] = useState<BasicState>(LOADING_DEFAULT_BASIC_STATE);
  const [homeState, setHomeState] = useState<BasicState>(LOADING_DEFAULT_BASIC_STATE)

  const core = useCore();
  const blockNumber = useBlockNumber();

  const fetchValue = useCallback(async () => {
    let totalHomeState: BigNumber = BigNumber.from('0');
    
    if(isHome){
      ids?.map(async(singleId) => {
        const contract: Contract = core.getTokenSaleSwapContract(token, singleId);
        if(contract){
          const price: BigNumber = await contract?.tokensAllocated();
          if(price !== null)
            totalHomeState = totalHomeState?.add(price);
        }
      })
      setHomeState({isLoading: false, value: totalHomeState})
    }else{
      const contract: Contract = core.getTokenSaleSwapContract(token, id);
      const price: BigNumber = await contract.tokensAllocated();
      setState({ isLoading: false, value: price });
    }

  }, [core, ids, id, token]);

  useEffect(() => {
    if (core) {
      fetchValue().catch((err) => {
        setState(NON_LOADING_DEFAULT_BASIC_STATE);
        console.error(`Failed to fetch tokens allocated ${token}: ${err.stack}`)
      });
    } else {
      setState(NON_LOADING_DEFAULT_BASIC_STATE);
    }
  }, [blockNumber, core, fetchValue, ids, id, token]);

  if(isHome) return homeState
  return state;
};

export default useGetTokensAllocated;
