import styled from "styled-components";
import { Grid } from "@material-ui/core";
import { useParams } from "react-router-dom";
import React, { useEffect, useMemo, useState } from "react";

import theme from "../../theme";

import Tab from "../../components/Tab";
import { isMobileGlobal } from "../../App";
import IconLoader from "../../components/IconLoader";
import TextWrapper from "../../components/TextWrapper";
import NoPageFound from "../../components/NoPageFound";
import ProjectInfoTab from "./components/ProjectInfoTabContent";
import ProjectStatsSection from "./components/ProjectStatsSection";
import ProjectStatusAndPriceDetails from "./components/ProjectStatusAndPriceDetails";

import { projects } from "../../config";

interface IProps {
  id: string;
}

const Scallop = (props: IProps) => {
  const {id} = props;
  const [selectedTab, setSelectedTab] = useState<0 | 1 | 2 | number>(0);

  const tokenSymbol = useMemo(() => {
    return projects[id];
  }, [id]);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const tabs = [
    {
      index: 0,
      key: 'Overview',
      text: 'Overview',
    },
    {
      index: 1,
      key: 'Timeline',
      text: 'IBO Timeline',
    },
    {
      index: 2,
      key: 'Vesting Schedule',
      text: 'Vesting',
    }
  ];

  if (!tokenSymbol) return <NoPageFound />;

  return (
    <div className={"custom-container"}>
      <div className={"m-b-20 single-line-center-start mo-custom-container"}>
        <a href={"/#/"}>
          <TextWrapper text={'Projects'} Fcolor={theme.color.transparent[200]} />
        </a>
        <IconLoader
          iconName={'ArrowRight'}
          iconType={'arrow'}
          className={"m-r-4 m-l-4"}
        />
        <TextWrapper
          text={id === "scallopmahax" ? "SCALLOP - MAHAX" : id.toUpperCase()}
          Fcolor={theme.color.transparent[100]}
        />
      </div>
      <Grid container spacing={3}>
        <Grid item lg={8} md={8} sm={12} xs={12}>
          <ProjectStatusAndPriceDetails
            tokenSymbol={tokenSymbol}
            projectName={id}
          />
          <ProjectVideo
            width="100%"
            height="404"
            src="https://www.youtube.com/embed/aHsctLnM-Bs"
            frameBorder="0"
            allow="accelerometer; autoplay;"
            allowFullScreen
          >
          </ProjectVideo>
          {
            isMobileGlobal &&
            <div className="m-b-24">
              <ProjectStatsSection tokenSymbol={tokenSymbol} id={id} />
            </div>
          }
          <section
            className={"w-100"}
            id={"tabs"}>
            <div className={"mo-custom-container"}>
              <Tab
                selectedTab={selectedTab}
                onTabChange={(d) => {
                  setSelectedTab(d);
                }}
                data={tabs}
              />
            </div>
            <div className="p-t-32">
              <ProjectInfoTab id={id} selectedTab={selectedTab} />
            </div>
          </section>
        </Grid>
        <Grid item lg={4} md={4} sm={12} xs={12}>
          {!isMobileGlobal && <ProjectStatsSection tokenSymbol={tokenSymbol} id={id} />}
        </Grid>
      </Grid>
    </div>
  );
}

export default Scallop;

const ProjectVideo = styled.iframe`
  width: 100%;
  height: 404px;
  margin-bottom: 24px;
  @media (max-width: 600px){
    height: 207px;
  }
`;
