import { useMemo } from "react";
import Loader from "react-spinners/BeatLoader";

import theme from "../../theme";

import DataField from "../DataField";

import useCore from "../../hooks/useCore";
import { IProjectIdProp, ITokenSymbolProp } from "../../utils/interface";
import { getDisplayBalanceToken } from "../../utils/formatBalance";
import useGetTokensForSale from "../../hooks/state/useGetTokensForSale";
import useGetTokensAllocated from "../../hooks/state/useGetTokensAllocated";
import { BigNumber } from "@ethersproject/bignumber";
interface Iprop{
  ids?: []
  isHome?: boolean
}

const TokenAllocationNumbersInToken = (props: ITokenSymbolProp & IProjectIdProp & Iprop) => {
  const core = useCore();
  const {id, ids, tokenSymbol, isHome} = props;

  const tokensForSale = useGetTokensForSale(tokenSymbol, id, isHome, ids);
  const tokensAllocated = useGetTokensAllocated(tokenSymbol, id, isHome, ids);

  const vesting = tokenSymbol === 'FORWARD'? 25: 40;

  const tokensAllocaltedFormatted = useMemo(() => {
    const val: BigNumber = tokensAllocated.value.mul(100).div(vesting);
    return Number(getDisplayBalanceToken(val, core.tokens[tokenSymbol], 3))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 3 });
  }, [tokensAllocated, core, tokenSymbol]);

  const tokensForSaleFormatted = useMemo(() => {
    const val: BigNumber = tokensForSale.value.mul(100).div(vesting);
    return Number(getDisplayBalanceToken(val, core.tokens[tokenSymbol], 3))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 3 })
  }, [tokensForSale, core, tokenSymbol]);

  return (
    <div>
      {
        tokensForSale.isLoading || tokensAllocated.isLoading
          ? <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
          : (
            <DataField
              label={`${tokensAllocaltedFormatted} / ${tokensForSaleFormatted} ${tokenSymbol.toUpperCase()}`}
              labelFontSize={12}
              labelFontColor={theme.color.transparent[100]}
            />
          )
      }
    </div>
  );
}

export default TokenAllocationNumbersInToken;
