import { BigNumber, Contract } from 'ethers';
import { useCallback, useEffect, useState } from 'react';

import useCore from '../useCore';
import { useBlockNumber } from '../../state/application/hooks';
import { MinAndMaxAllocationState } from '../../utils/interface';
import { LOADING_DEFAULT_ALLOCATION_STATE, NON_LOADING_DEFAULT_ALLOCATION_STATE } from '../../utils/constants';

const useGetMinAndMaxAllocation = (token: string, id: string) => {
  const [state, setState] = useState<MinAndMaxAllocationState>(LOADING_DEFAULT_ALLOCATION_STATE);

  const core = useCore();
  const blockNumber = useBlockNumber();

  const fetchValue = useCallback(async () => {
    const contract: Contract = core.getTokenSaleSwapContract(token, id);
    const minAllocation: BigNumber = await contract.individualMinimumAmount();
    const maxAllocation: BigNumber = await contract.individualMaximumAmount();

    setState({ isLoading: false, minAllocation, maxAllocation });
  }, [core, id, token]);

  useEffect(() => {
    if (core) {
      fetchValue().catch((err) => {
        setState(NON_LOADING_DEFAULT_ALLOCATION_STATE);
        console.error(`Failed to fetch min and max allocations ${token}: ${err.stack}`)
      });
    } else {
      setState(NON_LOADING_DEFAULT_ALLOCATION_STATE);
    }
  }, [blockNumber, core, fetchValue, id, token]);

  return state;
};

export default useGetMinAndMaxAllocation;
