import { useMemo } from "react";

import theme from "../../theme";

import DataField from "../DataField";
import TextWrapper from "../TextWrapper";

import { ITokenSymbolProp } from "../../utils/interface";
import { getDisplayBalance } from "../../utils/formatBalance";
import useGetTokenSalePrice from "../../hooks/state/useGetTokenSalePrice";
import useConvertUSDtoBNB from "../../hooks/state/useConvertUSDtoBNB";

interface IProps {
  isDataField?: boolean;
}

const TokenPrice = (props: ITokenSymbolProp & IProps) => {
  const { isDataField = false, tokenSymbol } = props;

/*  const tradeValue = useGetTokenSalePrice(tokenSymbol, id);

  const tokenPriceInBNBFormatted = useMemo(() => {
    return Number(getDisplayBalance(tradeValue.value, 18, 6))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 6 });
  }, [tradeValue]);*/

  const dollarValue = useMemo(() => {
    if (tokenSymbol === 'SCLP') {
      return "0.15"
    } else if (tokenSymbol === 'FORWARD') {
      return "0.005"
    } else {
      return "-"
    }
  }, [tokenSymbol])

  const bnbValue = useConvertUSDtoBNB(dollarValue)

  if (isDataField) return (
    <div className="m-b-16">
      <DataField
        label={tokenSymbol === 'SCLP'? `${tokenSymbol.toUpperCase()} Price`: '1 FORWARD'}
        value={`$${dollarValue}`}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
      />
      <DataField
        value={`~${
          tokenSymbol === 'SCLP'
            ? Number(bnbValue.value).toLocaleString('en-US', { minimumFractionDigits: 6, maximumFractionDigits: 6 })
            : dollarValue
        } ${props.tokenSymbol === 'FORWARD' ? 'BUSD' : 'BNB'}`}
        isValueLoading={bnbValue.isLoading}
        valueFontSize={12}
        valueFontWeight={600}
        valueFontColor={theme.color.transparent[100]}
      />
    </div>
  );

  return (
    <div>
      <TextWrapper
        text={
          `1 ${tokenSymbol.toUpperCase()} = $ ${dollarValue}`
        }
        fontSize={12}
        fontWeight={600}
        Fcolor={theme.color.transparent[100]}
      />
    </div>
  );
}

export default TokenPrice;
