import { BigNumber, Contract } from 'ethers';
import { useCallback, useEffect, useState } from 'react';

import useCore from '../useCore';
import { BasicState } from '../../utils/interface';
import { useBlockNumber } from '../../state/application/hooks';
import { LOADING_DEFAULT_BASIC_STATE, NON_LOADING_DEFAULT_BASIC_STATE } from '../../utils/constants';

const useGetAvailableTokens = (token: string, id: string) => {
  const [state, setState] = useState<BasicState>(LOADING_DEFAULT_BASIC_STATE);

  const core = useCore();
  const blockNumber = useBlockNumber();

  const fetchValue = useCallback(async () => {
    const contract: Contract = core.getTokenSaleSwapContract(token, id);
    const availableTokens: BigNumber = await contract.availableTokens();
    setState({ isLoading: false, value: availableTokens });
  }, [core, id, token]);

  useEffect(() => {
    if (core) {
      fetchValue().catch((err) => {
        setState(NON_LOADING_DEFAULT_BASIC_STATE);
        console.error(`Failed to fetch available tokens for sale of ${token}: ${err.stack}`)
      });
    } else {
      setState(NON_LOADING_DEFAULT_BASIC_STATE);
    }
  }, [blockNumber, core, id, fetchValue, token]);

  return state;
};

export default useGetAvailableTokens;
