import ProjectOverview from "./ProjectOverview";
import ProjectTimeline from "./ProjectTimeline";

import { IProjectIdProp } from "../../../utils/interface";
import ProjectVesting from "./ProjectVesting";

interface IProps {
  selectedTab: number;
}

const ProjectInfoTab = (props: IProps & IProjectIdProp) => {
  const { selectedTab, id } = props;

  if (selectedTab === 1) return <ProjectTimeline id={id} />;
  if (selectedTab === 2) return <ProjectVesting />;

  return <ProjectOverview id={id} />;
}

export default ProjectInfoTab;
