import { useMemo } from "react";
import { BigNumber } from "ethers";

import theme from "../../theme";

import DataField from "../DataField";

import useCore from "../../hooks/useCore";
import { getDisplayBalance } from "../../utils/formatBalance";
import { IProjectIdProp, ITokenSymbolProp } from "../../utils/interface";
import useGetTokensForSale from "../../hooks/state/useGetTokensForSale";
import useGetTokenSalePrice from "../../hooks/state/useGetTokenSalePrice";
import useGetTokensAllocated from "../../hooks/state/useGetTokensAllocated";
interface Iprop{
  ids?: []
  isHome?: boolean
}

const AmountRaised = (props: ITokenSymbolProp & IProjectIdProp & Iprop ) => {
  const core = useCore();
  const {id, ids, tokenSymbol, isHome} = props;
  
  const tradeValue = useGetTokenSalePrice(tokenSymbol, id, isHome, ids);
  const tokensForSale = useGetTokensForSale(tokenSymbol, id, isHome, ids);
  const tokensAllocated = useGetTokensAllocated(tokenSymbol, id, isHome, ids);

  console.log('tokensAllocated', tokensAllocated)

  const bnbRaisedFormatted = useMemo(() => {
    const raised: BigNumber = tokensAllocated.value
      .mul(tradeValue.value)
      .div(BigNumber.from(10).pow(core.tokens[tokenSymbol].decimal));

    return Number(getDisplayBalance(raised, 18, 3))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 });
  }, [tokensAllocated, core, tokenSymbol, tradeValue]);

  const percentCompleted = useMemo(() => {

    if (tokensAllocated.value.gt(tokensForSale.value)) return BigNumber.from(1e6).mul(100);
    if (tokensAllocated.value.lte(0) || tokensForSale.value.lte(0)) return BigNumber.from(1e6).mul(100).mul(0);
    return tokensAllocated.value
      .mul(1e6)
      .mul(100)
      .div(tokensForSale.value)

  }, [tokensAllocated, tokensForSale]);

  return (
    <div>
      <DataField
        label={`${bnbRaisedFormatted} ${tokenSymbol === 'FORWARD' ? 'BUSD' : 'BNB'} Raised`}
        value={`${Number(getDisplayBalance(percentCompleted, 6, 6)).toFixed(2)}%`}
        isValueLoading={tokensAllocated.isLoading || tradeValue.isLoading || tokensForSale.isLoading}
        labelFontSize={18}
        labelFontColor={"white"}
        valueFontSize={18}
        valueFontColor={theme.color.transparent[100]}
        className="m-b-4"
      />
    </div>
  );
}

export default AmountRaised;
