import { useMemo } from "react";

import theme from "../../theme";

import DataField from "../DataField";

import { IProjectIdProp, ITokenSymbolProp, isHome } from "../../utils/interface";
import { getDisplayBalance } from "../../utils/formatBalance";

import useGetTotalRaiseCost from "../../hooks/state/useGetTotalRaiseCost";
import useConvertUSDtoBNB from "../../hooks/state/useConvertUSDtoBNB";

const TotalRaised = (props: ITokenSymbolProp & IProjectIdProp & isHome) => {
 /* const totalRaiseCost = useGetTotalRaiseCost(props.tokenSymbol, props.id);

  const formattedValue: string = useMemo(() => {
    return Number(getDisplayBalance(totalRaiseCost.value, 18, 6))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })
  }, [totalRaiseCost]);*/

  const data = useMemo(() => {
    if (props.id.toLowerCase() === 'scallop') return '$140k'
    else if (props.id.toLowerCase() === 'scallopmahax') return '$72k'
    else if (props.id.toLowerCase() === 'forward') return '$20k'
    else if (props.id.toLowerCase() === 'forwardmahax') return '$35k'
    else if (props.id.toLowerCase() === 'forwardmahax2') return '$35k'
    else if (props.id.toLowerCase() === 'forwardsquareup') return '$10k'
    else return '0'
  }, [props]);

  const bnbValueUSD = useMemo(() => {
    if (props.id.toLowerCase() === 'scallop') return '140000'
    else if (props.id.toLowerCase() === 'scallopmahax') return '72000'
    else if (props.id.toLowerCase() === 'forward') return '20000'
    else if (props.id.toLowerCase() === 'forwardmahax') return '35000'
    else if (props.id.toLowerCase() === 'forwardmahax2') return '35000'
    else if (props.id.toLowerCase() === 'forwardsquareup') return '10000'
    else return '0'
  }, [props]);

  const bnbValue = useConvertUSDtoBNB(bnbValueUSD);

  const homeData = useMemo(() => {
    if(props.tokenSymbol === 'FORWARD') return '$100k'
    else if(props.tokenSymbol === 'SCLP') return '$212k'
    else return '0'
  }, [props])

  const homeBnbValueUSD = useMemo(() => {
    if(props.tokenSymbol === 'FORWARD') return '100000'
    else if(props.tokenSymbol === 'SCLP') return '212000'
    else return '0'
  }, [props])

  const homeBnbValue = useConvertUSDtoBNB(homeBnbValueUSD)

  if(props.isHome){
    return (
      <div className="m-b-16">
        <DataField
          label={"Target raise"}
          isValueLoading={false}
          value={homeData}
          labelFontSize={18}
          labelFontColor={theme.color.transparent[100]}
          valueFontSize={18}
          valueFontColor={"white"}
          valueFontWeight={600}
        />
        <DataField
          value={`~${
            props.tokenSymbol === 'SCLP'
              ? Number(homeBnbValue.value).toLocaleString('en-US', {
                  minimumFractionDigits: 2,
                  maximumFractionDigits: 2})
              : `${homeData}`
          } ${props.tokenSymbol === 'FORWARD' ? 'BUSD' : 'BNB'}`}
          isValueLoading={homeBnbValue.isLoading}
          valueFontSize={12}
          valueFontWeight={600}
          valueFontColor={theme.color.transparent[100]}
        />
      </div>
    );
  }

  return (
    <div className="m-b-16">
      <DataField
        label={"Target raise"}
        isValueLoading={false}
        value={data}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
      />
      <DataField
        value={`~${
          props.tokenSymbol === 'SCLP'
            ? Number(bnbValue.value).toLocaleString('en-US', {
                minimumFractionDigits: 2,
                maximumFractionDigits: 2})
            : `${data}`
        } ${props.tokenSymbol === 'FORWARD' ? 'BUSD' : 'BNB'}`}
        isValueLoading={bnbValue.isLoading}
        valueFontSize={12}
        valueFontWeight={600}
        valueFontColor={theme.color.transparent[100]}
      />
    </div>
  );
}

export default TotalRaised;
