
import moment from "moment";

import theme from "../../theme";

import DataField from "../DataField";
import { IProjectIdProp, ITokenSymbolProp } from "../../utils/interface";
import useGetStartAndEndDate from "../../hooks/state/useGetStartAndEndDate";
import {useMemo} from "react";

interface IProps {
  format?: string;
}

const IDOOn = (props: IProps & ITokenSymbolProp & IProjectIdProp) => {
  const { format = 'MMM Do YYYY, h:mm A', tokenSymbol, id } = props;

  const startAndEndDate = useGetStartAndEndDate(tokenSymbol, id);

  return (
    <div>
      {startAndEndDate && <DataField
        label={"IBO on"}
        value={
          moment(new Date(startAndEndDate.startDate * 1000)).format(format)
        }
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
        className="m-b-16"
      />}
    </div>
  );
}

export default IDOOn;
