import styled from "styled-components";
import { useWallet } from "use-wallet";
import { Grid } from "@material-ui/core";
import copyText from 'copy-to-clipboard';
import Loader from "react-spinners/BeatLoader";
import React, { useEffect, useState } from "react";

import Button from "../../../../Button";
import theme from "../../../../../theme";
import ChooseWallet from "./ChooseWallet";
import DataField from "../../../../DataField";
import IconLoader from "../../../../IconLoader";
import TextWrapper from "../../../../TextWrapper";
import ConfirmationModal from "../../../../ConfirmationModal";

import MetamaskImg from "../../../../../assets/images/Metamask.svg";
import walletConnectimg from '../../../../../assets/images/walletConnect.png';

import { truncateMiddle } from "../../../../../utils";
import useBalance from "../../../../../hooks/useBalance";
import { getDisplayBalance } from "../../../../../utils/formatBalance";
import useGetMAHAXBalance from "../../../../../hooks/state/useGetMAHAXBalance";
import useTokenBalanceOf from "../../../../../hooks/useTokenBalanceOf";
import useCore from "../../../../../hooks/useCore";
import useFaucet from '../../../../../hooks/callbacks/useFaucet';

const WalletInfo = () => {
  const [isCopied, setIsCopied] = useState<boolean>(false);
  const [showWalletOption, setShowWalletOption] = useState<boolean>(false);
  const [showConfirmationModal, setShowConfirmationModal] = useState<boolean>(false);

  const core = useCore();
  const bnbBalance = useBalance();
  const { account, connector } = useWallet();
  const busdBalance = useTokenBalanceOf(core.tokens.BUSD, account || "")

  const faucetCb = useFaucet();

  useEffect(() => {
    if (isCopied) {
      const hide = setTimeout(() => {
        setIsCopied(false)
      }, 500)

      return () => {
        clearTimeout(hide)
      }
    }
  }, [isCopied, setIsCopied]);

  const MAHAXValue = useGetMAHAXBalance(account);

  return (
    <>
      <ChooseWallet
        openModal={showWalletOption}
        onClose={() => setShowWalletOption(false)}
      />
      <MainContainer>
        <ConfirmationModal
          modalOpen={showConfirmationModal}
          onClose={() => setShowConfirmationModal(false)}
          modalTitle={'Disconnect Wallet'}
          title={`Are you sure you want to disconnect ${truncateMiddle(account || '', 15)}?`}
          subtitle={`${account || ''}`}
          yesText={'Disconnect'}
          noText={'Cancel'}
          yesAction={() => {
            setShowConfirmationModal(false);
            window.location.reload();
          }}
          noAction={() => setShowConfirmationModal(false)}
        />
        <WalletHeader className="single-line-center-between bottom-divider">
          <TextWrapper
            text={'Your Account'}
            fontWeight={600}
            fontSize={16}
          />
          <div className="single-line-center-start">
            <img src={
              connector === 'walletconnect'
                ? walletConnectimg
                : MetamaskImg
            }
              height={32}
              className="m-r-8"
              alt="metamask"
            />
            <TextWrapper
              text={`${truncateMiddle(account || '', 12, '...')}`}
              fontWeight={600}
              fontSize={16}
              className="m-r-8"
            />
            {
              isCopied
                ? <IconLoader
                  iconName={'Copied'}
                />
                : <IconLoader
                  iconName={'Copy'}
                  onClick={() => {
                    const didCopy = copyText(account?.toString() || '');
                    setIsCopied(didCopy);
                  }}
                  className="pointer"
                />
            }
          </div>
        </WalletHeader>
        <MAHAXContain className={"bottom-divider"}>
          <DataField
            label={"Your MAHAX"}
            value={Number(MAHAXValue.value).toLocaleString('en-US', { maximumFractionDigits: 2 })}
            isValueLoading={MAHAXValue.isLoading}
            valueTag={"MAHAX"}
            labelFontWeight={600}
            labelFontSize={16}
            labelFontColor={"#FFF"}
            valueFontWeight={600}
            valueFontSize={16}
            valueFontColor={theme.color.transparent[100]}
          />
        </MAHAXContain>
        <WalletBody>
          <div className="single-line-center-between m-b-20">
            <div className="single-line-center-start">
              <IconLoader iconName={'BNB'} iconType="tokenSymbol" className="m-r-12" height={44} width={44} />
              {
                bnbBalance.isLoading
                  ? <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
                  : <TextWrapper
                    text={
                      `${Number(getDisplayBalance(bnbBalance.value, 18, 3)).toLocaleString('en-US', { maximumFractionDigits: 2 })} BNB`
                    }
                    fontWeight={600}
                  />
              }
            </div>
            <TextWrapper
              text={""}
              fontWeight={600}
              Fcolor={theme.color.transparent[100]}
            />
          </div>
          <div className="single-line-center-between m-b-20">
            <div className="single-line-center-start">
              <IconLoader iconName={'BUSD'} iconType="tokenSymbol" className="m-r-12" height={44} width={44} />
              {
                busdBalance.isLoading
                  ? <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
                  : <TextWrapper
                    text={
                      `${Number(getDisplayBalance(busdBalance.value, 18, 3)).toLocaleString('en-US', { maximumFractionDigits: 2 })} BUSD`
                    }
                    fontWeight={600}
                  />
              }
              <br />
             {/* {
                process.env.REACT_APP_MODE !== 'production' &&
                <Button
                  onClick={faucetCb}
                >
                  Get faucet
                </Button>
              }*/}
            </div>
            <TextWrapper
              text={""}
              fontWeight={600}
              Fcolor={theme.color.transparent[100]}
            />
          </div>
          <Grid container spacing={1}>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button variant={'transparent'} onClick={() => setShowWalletOption(true)}>Change</Button>
            </Grid>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button onClick={() => setShowConfirmationModal(true)}>Disconnect</Button>
            </Grid>
          </Grid>
        </WalletBody>
      </MainContainer>
    </>
  );
}

export default WalletInfo;

const MainContainer = styled.div`
  width: 100%;
`;

const WalletHeader = styled.div`
  padding-bottom:16px ;
`;

const WalletBody = styled.div`
  padding: 24px 0 0 0;
`;

const MAHAXContain = styled.div`
  padding: 24px 0;
`;
