import { useWallet } from 'use-wallet';
import { BigNumber, Contract } from 'ethers';
import { useCallback, useEffect, useState } from 'react';

import useCore from '../useCore';
import { MyPurchasesState } from '../../utils/interface';
import { useBlockNumber } from '../../state/application/hooks';
import { LOADING_DEFAULT_MYPURCHASES_STATE, NON_LOADING_DEFAULT_MYPURCHASES_STATE } from '../../utils/constants';

const useGetMyPurchases = (token: string, id: string) => {
  const [state, setState] = useState<MyPurchasesState>(LOADING_DEFAULT_MYPURCHASES_STATE);

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();

  const fetchValue = useCallback(async () => {
    const contract: Contract = core.getTokenSaleSwapContract(token, id);
    const purchasesIds: BigNumber[] = await contract.getMyPurchases(account);
    setState({ isLoading: false, value: purchasesIds });
  }, [core, account, id, token]);

  useEffect(() => {
    if (core && account) {
      fetchValue().catch((err) => {
        setState(NON_LOADING_DEFAULT_MYPURCHASES_STATE);
        console.error(`Failed to fetch my purchases for sale of ${token}: ${err.stack}`)
      });
    } else {
      setState(NON_LOADING_DEFAULT_MYPURCHASES_STATE);
    }
  }, [blockNumber, core, account, id, fetchValue, token]);

  return state;
};

export default useGetMyPurchases;
