import { useWallet } from 'use-wallet';
import { Contract } from '@ethersproject/contracts';
import { useCallback, useEffect, useState } from 'react';

import useCore from '../useCore';
import { BasicBoolState } from '../../utils/interface';
import { useBlockNumber } from '../../state/application/hooks';
import { LOADING_DEFAULT_BASICBOOL_STATE, NON_LOADING_DEFAULT_BASICBOOL_STATE } from '../../utils/constants';

const useGetIsWhitelisted = (token: string, id: string) => {
  const [state, setState] = useState<BasicBoolState>(LOADING_DEFAULT_BASICBOOL_STATE);

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();

  const fetchValue = useCallback(async () => {
    const contract: Contract = core.getTokenSaleSwapContract(token, id);
    const cost: boolean = await contract.whitelist(account);
    setState({ isLoading: false, value: cost });
  }, [core, account, id, token]);

  useEffect(() => {
    if (core && account) {
      fetchValue().catch((err) => {
        setState(NON_LOADING_DEFAULT_BASICBOOL_STATE);
        console.error(`Failed to fetch token raised cost of ${token}: ${err.stack}`)
      });
    } else {
      setState(NON_LOADING_DEFAULT_BASICBOOL_STATE);
    }
  }, [blockNumber, core, account, id, fetchValue, token]);

  return state;
};

export default useGetIsWhitelisted;
