import { useCallback } from 'react';
import { BigNumber, Contract } from 'ethers';
import { useTransactionAdder } from '../../state/transactions/hooks';

import useCore from '../useCore';
import { useAddPopup } from '../../state/application/hooks';
import formatErrorMessage from '../../utils/formatErrorMessage';
import { getDisplayBalanceToken } from '../../utils/formatBalance';
import {IDMapping} from "../../views/Forward/mapping";

const useRedeem = (
  token: string,
  amount: BigNumber,
  id: string
) => {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();

  const action = useCallback(async (minedCb?: () => void, successCb?: () => void, errorCb?: () => void): Promise<void> => {
    try {
      const gasOptions = core.gasOptions();
      const contract: Contract = core.getTokenSaleSwapContract(token, id);

      const tx = await contract.redeemTokens(
        {
          ...gasOptions,
        }
      );

      const identifierText = IDMapping[id] || id;
      addTransaction(
        tx,
        {
          summary: `Claim ${Number(getDisplayBalanceToken(amount, core.tokens[token], 3))
            .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })} from ${identifierText} Sale.`
        }
      );

      if (minedCb) minedCb();
      const txResponse = await tx.wait();
      if (successCb && txResponse.status === 1) successCb();
      if (errorCb && txResponse.status !== 1) errorCb();
    } catch (e: any) {
      if (errorCb) errorCb();
      addPopup({
        error: {
          message: formatErrorMessage(e?.data?.message || e?.message),
          stack: e?.stack,
        },
      });
    }
  }, [core, amount, addPopup, id, token, addTransaction]);

  return action;
}

export default useRedeem;
