import React from "react";
import { useWallet } from "use-wallet";

import Button from "../../../components/Button";

import { IProjectIdProp } from "../../../utils/interface";

interface Iprops {
  walletAddress: string;
  emailAddress: string;
  onProcessStart: () => void;
  onProcessClose: () => void;
  onProcessCancel: () => void;
  onProcessSuccess: () => void;
}

const KycProcess = (props: Iprops & IProjectIdProp) => {
  const {
    onProcessStart,
    onProcessCancel,
    onProcessClose,
    onProcessSuccess,
    id,
    emailAddress = "",
    walletAddress,
  } = props;

  const { account } = useWallet();

  const loadBlockpassWidget = () => {
    const ran = Math.random();

    // @ts-ignore
    const blockpass = new window.BlockpassKYCConnect(
      process.env.REACT_APP_BLOCK_PASS_CLIENT_ID,
      {
        email: emailAddress,
        refId: account + "-" + id + ran,
        local_user_id: account + "-" + id + ran,
        mainColor: "darkGrey",
      }
    )

    blockpass.startKYCConnect();

    blockpass.on('KYCConnectLoad', () => {
      console.log("KYCConnectLoad")
      onProcessStart();
    });

    blockpass.on('KYCConnectClose', () => {
      console.log("KYCConnectClose")
      onProcessClose();
    });

    blockpass.on('KYCConnectCancel', () => {
      console.log("KYCConnectCancel")
      onProcessCancel();
    });

    blockpass.on('KYCConnectSuccess', () => {
      console.log("KYCConnectSuccess")
      onProcessSuccess();
    });
  }

  return (
    <Button
      tracking_id={"blockpass-kyc-connect"}
      onClick={loadBlockpassWidget}
    >
      Verify with Blockpass
    </Button>
  );
}

export default KycProcess;
