import { useWallet } from "use-wallet";
import { useCallback, useEffect, useState } from 'react';

import { BasicStateString } from "../../utils/interface";
import callApi from "../../utils/apiCaller";
import { LOADING_DEFAULT_BASIC_STATE_STRING, NON_LOADING_DEFAULT_BASIC_STATE_STRING } from "../../utils/constants";

const useGetMAHAXBalance = (walletAddress: string | null) => {
  const [state, setState] = useState<BasicStateString>(LOADING_DEFAULT_BASIC_STATE_STRING);

  const { account } = useWallet();

  const fetchValue = useCallback(async () => {
    setState(LOADING_DEFAULT_BASIC_STATE_STRING);
    callApi("escrow/mahax/balance", "POST", {
      walletAddress: walletAddress,
    })
      .then((value) => {
        setState({
          isLoading: false,
          value: value?.mahaxBalance ? value?.mahaxBalance : "",
        });
      })
  }, [walletAddress]);

  useEffect(() => {
    if (account) {
      try {
        fetchValue().catch((err) => {
          setState(NON_LOADING_DEFAULT_BASIC_STATE_STRING);
          console.error(`Failed to fetch balance of MAHAX: ${err.stack}`)
        });
      }
      catch (e) {
        setState(NON_LOADING_DEFAULT_BASIC_STATE_STRING);
        console.error(`Failed to fetch balance of MAHAX: ${e}`)
      }
    } else {
      setState(NON_LOADING_DEFAULT_BASIC_STATE_STRING);
    }
  }, [account, fetchValue]);

  return state;
};

export default useGetMAHAXBalance;
