import { useMemo, useEffect, useState } from "react";
import styled from "styled-components";
import makeUrls, { TCalendarEvent } from "add-event-to-calendar";

import IDOOn from "../IDOOn";
import TotalRaised from "../TotalRaised";
import AmountRaised from "../AmountRaised";
import PercentCompleted from "../PercentCompleted";
import MinAndMaxAllocation from "../MinAndMaxAllocation";
import TokenAllocationNumbersInToken from "../TokenAllocationNumbersInToken";
import google from '.././../assets/images/googleCalendar.svg'

import { DAY_IN_MS } from "../../utils/constants";
import { IProjectIdProp, ITokenSymbolProp, IProjectIdsArrayProp } from "../../utils/interface";
import useGetAvailableTokens from "../../hooks/state/useGetAvailableTokens";
import useGetStartAndEndDate from "../../hooks/state/useGetStartAndEndDate";
import {Mixpanel} from "../../analytics/Mixpanel";
import HomeInvestorsCount from '../HomeInvestrosCount';

interface ProjectCardProps{
  arrayOfIds: any[];
}

const HomeStatsSection = (props: ITokenSymbolProp & IProjectIdsArrayProp & ProjectCardProps) => {
  const { tokenSymbol, ids, arrayOfIds } = props;

  const availableTokens = useGetAvailableTokens(tokenSymbol, ids[0]);
  const startAndEndDate = useGetStartAndEndDate(tokenSymbol, ids[0]);

  const [calendarLink, setLink] = useState('');

  useEffect(() => {
    const onClick = () => {
      let event: TCalendarEvent = {
        name: `${ids[0].toUpperCase()} IBO`,
        location: `https://${window.location.hostname}/#/projects/${ids[0].toLowerCase()}`,
        details: `Reminder for ${ids[0].toUpperCase()} IBO`,
        startsAt: new Date(startAndEndDate.startDate * 1000).toUTCString(),
        endsAt: new Date(startAndEndDate.endDate * 1000).toUTCString(),
      };
      setLink(makeUrls(event).google);
    };
    onClick();

  }, [startAndEndDate, ids, tokenSymbol]);

  console.log('HomeStatsSectionids', ids)

  console.log('arrayOfIds', arrayOfIds)

  const status: string = useMemo(() => {
    if (startAndEndDate.startDate * 1000 > Date.now()) return "upcoming"

    if (Date.now() < startAndEndDate.endDate * 1000 && availableTokens.value.gt(0)) {
      if (startAndEndDate.endDate * 1000 - Date.now() <= DAY_IN_MS)
        return "24hrsLeft";
      else
        return "live";
    }

    return "closed";
  }, [startAndEndDate, availableTokens]);

  if (status === "upcoming") {
    return (
      <div>
        <TotalRaised id={ids[0]} tokenSymbol={tokenSymbol} isHome={true} />
        <IDOOn id={ids[0]} tokenSymbol={tokenSymbol} format={'MMM Do YYYY, h:mm A'} />
        <HeaderButton onClick={() => {
            Mixpanel.track(`buttonClick:${ids[0]}_add_to_calendar_project_page`)
            window.open(calendarLink, '_blank')
          }}>
            <img src={google} alt="calendar" height={24} />
            <span style={{ marginLeft: 8 }}>Add to Calendar</span>
        </HeaderButton>
      </div>
    )
  }

  if (status === "24hrsLeft" || status === "live") {
    
    if(tokenSymbol === 'FORWARD'){
      return (
        <div>
          <div className={"m-b-16"}>
            <AmountRaised id={ids[0]} ids={arrayOfIds[0]} tokenSymbol={tokenSymbol} isHome={true} />
            <PercentCompleted id={ids[0]} ids={arrayOfIds[0]} tokenSymbol={tokenSymbol} isHome={true} />
            <TokenAllocationNumbersInToken id={ids[0]} ids={arrayOfIds[0]} tokenSymbol={tokenSymbol} isHome={true}/>
          </div>
          <TotalRaised id={ids[0]} tokenSymbol={tokenSymbol} isHome={true} />
          <HomeInvestorsCount tokenSymbol={tokenSymbol} />
        </div>
      );
    } else{
      return (
        <div>
          <div className={"m-b-16"}>
            <AmountRaised id={ids[0]} ids={arrayOfIds[1]} tokenSymbol={tokenSymbol} isHome={true} />
            <PercentCompleted id={ids[0]} ids={arrayOfIds[1]} tokenSymbol={tokenSymbol} isHome={true} />
            <TokenAllocationNumbersInToken id={ids[0]} ids={arrayOfIds[0]} tokenSymbol={tokenSymbol} isHome={true}/>
          </div>
          <TotalRaised id={ids[0]} tokenSymbol={tokenSymbol} isHome={true}/>
          <HomeInvestorsCount tokenSymbol={tokenSymbol} />
        </div>
      );
    }

  }

  if(status === 'closed'){
    if(tokenSymbol === 'FORWARD'){
      return (
        <div>
          <div className={"m-b-16"}>
            <AmountRaised id={ids[0]} ids={arrayOfIds[0]} tokenSymbol={tokenSymbol} isHome={true} />
            <PercentCompleted id={ids[0]} ids={arrayOfIds[0]} tokenSymbol={tokenSymbol} isHome={true} />
            <TokenAllocationNumbersInToken id={ids[0]} ids={arrayOfIds[0]} tokenSymbol={tokenSymbol} isHome={true} />
          </div>
          <TotalRaised id={ids[0]} tokenSymbol={tokenSymbol} isHome={true} />
          <HomeInvestorsCount tokenSymbol={tokenSymbol} />
        </div>
      );
    } else{
      return (
        <div>
          <div className={"m-b-16"}>
            <AmountRaised id={ids[0]} ids={arrayOfIds[1]} tokenSymbol={tokenSymbol} isHome={true} />
            <PercentCompleted id={ids[0]} ids={arrayOfIds[1]} tokenSymbol={tokenSymbol} isHome={true} />
            <TokenAllocationNumbersInToken id={ids[0]} ids={arrayOfIds[0]} tokenSymbol={tokenSymbol} isHome={true} />
          </div>
          <TotalRaised id={ids[0]} tokenSymbol={tokenSymbol} isHome={true} />
          <HomeInvestorsCount tokenSymbol={tokenSymbol} />
        </div>
      );
    }
  }

  return <div></div>
}

export default HomeStatsSection;

const HeaderButton = styled.div`
  background: rgba(97, 134, 242, 0.32);
  border-radius: 8px;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: rgba(255, 255, 255, 0.88);
  display: flex;
  flex-direction: row;
  padding: 15px;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 100%;
`;
