import React from "react";
import { Redirect, Route, Switch, BrowserRouter as Router,} from "react-router-dom";

import {listOfProjects} from './config'
import Home from "./views/Home";
import Page from "./components/Page";
import NoPageFound from "./components/NoPageFound";
import Footer from "./views/Home/components/Footer";
import HeroSection from "./views/Home/components/HeroSection";
import BlockPass from "./views/BlockPass";
import Projects from "./views/Projects";
import ProjectInfoCard from './views/Home/components/ProjectInfoCard';
import TextWrapper from './components/TextWrapper';
import IconLoader from './components/IconLoader';
import theme from './theme';

const Navigation = () => {
  return (
    <>
     <Switch>
      <Route exact path="/">
        <div>
          <HeroSection />
          <Page>
            <Home />
          </Page>
          <Footer />
        </div>
      </Route>
      <Route exact path="/projects/:id">
        <Page>
          <Projects />
        </Page>
      </Route>

      {Object.keys(listOfProjects).map((project: string, i: number) => {
        console.log('navigation', project, listOfProjects[project]);
          return(
            <Route exact path={`/projectlist/${project}`}>
              <Page>
                <div className={"m-b-20 single-line-center-start mo-custom-container"}>
                  <a href={"/#/"}>
                    <TextWrapper text={'Projects'} Fcolor={theme.color.transparent[200]} />
                  </a>
                  <IconLoader
                    iconName={'ArrowRight'}
                    iconType={'arrow'}
                    className={"m-r-4 m-l-4"}
                  />
                  <TextWrapper
                    text={`${project} Pools`}
                    Fcolor={theme.color.transparent[100]}
                  />
                </div>
                {
                  listOfProjects[project].projectName.map((name) => (
                    <ProjectInfoCard tokenSymbol={project} id={name} />
                  ))
                }
              </Page>
              <Footer />
            </Route>
          )
      })}
      {/*<Route exact path="/projects/:id/kyc-verification">
        <Page>
          <BlockPass />
        </Page>
      </Route>*/}
      <Route exact path="/kyc">
        <Page>
            <BlockPass />
          </Page>
        </Route>
        <Route exact path="*">
          <NoPageFound />
        </Route>
        <Redirect to="/" />
      </Switch>
    </>
   
  );
}

export default Navigation;
